import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  OutlinedInput,
} from '@material-ui/core';

// Import Material-UI props interface
import { SelectProps } from '@material-ui/core/Select';
import { SelectInputProps } from '@material-ui/core/Select/SelectInput';

interface MenuItemObject {
  name: string;
  value: string;
}

/**
 * label - name label displayed above the dropdown list
 * menuItems - array of MenuItemObject that contains the "name" and "value" of the dropdown list
 * width - width of the dropdown list
 */
interface BasicSelectProps {
  id?: string;
  label?: string;
  menuItems?: Array<MenuItemObject>;
  width?: string;
  value?: SelectProps['value'];
  onChange?: SelectInputProps['onChange'];
  error?: boolean;
  disabled?: boolean;
}

const useStyles = makeStyles({
  inputLabel: {
    color: '#272e4c',
  },
  formControl: (props: BasicSelectProps) => ({
    marginTop: '10px',
    marginBottom: '20px',
    minWidth: props.width,
  }),
  menuItem: {
    color: '#272e4c',
  },
  outlinedInput: {
    padding: '12.5px 14px',
    color: '#272e4c',
  },
});

const BasicSelect = (props: BasicSelectProps) => {
  const classes = useStyles(props);

  const menuItemsNode = () => {
    let nodes: Array<React.ReactNode> = [];

    if (props.menuItems) {
      props.menuItems.map((item, index) => {
        nodes.push(
          <MenuItem key={index} value={item.value} className={classes.menuItem}>
            {item.name}
          </MenuItem>,
        );
        return null; // Fix for array-callback-return
      });
    }

    return nodes;
  };

  return (
    <React.Fragment>
      <InputLabel htmlFor={props.id} className={classes.inputLabel}>
        {props.label}
      </InputLabel>
      <FormControl className={classes.formControl}>
        <Select
          input={
            <OutlinedInput
              id={props.id}
              labelWidth={0}
              classes={{
                input: classes.outlinedInput,
              }}
            />
          }
          value={props.value}
          onChange={props.onChange}
          error={props.error ? props.error : false}
          disabled={props.disabled ? props.disabled : false}
        >
          {menuItemsNode()}
        </Select>
      </FormControl>
    </React.Fragment>
  );
};

export default BasicSelect;
