import React from 'react';
import { useState } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import { InputLabel, TextField, FormHelperText } from '@material-ui/core';

// Import Material-UI props interface
import {
  BaseTextFieldProps,
  TextFieldProps,
  OutlinedTextFieldProps,
} from '@material-ui/core/TextField';

/**
 * label - name label displayed above the text field
 * width - width of the text field
 */
interface BasicTextFieldProps {
  id?: string;
  label?: string;
  width?: string;
  value?: BaseTextFieldProps['value'];
  onChange?: OutlinedTextFieldProps['onChange'];
  TextFieldProps?: TextFieldProps;
  type?: string;
  error?: boolean;
  disabled?: boolean;
  InputProps?: any;
  error_message?: string;
}

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
  },
  inputLabel: {
    color: '#272e4c',
  },
  textField: (props: BasicTextFieldProps) => ({
    marginTop: '10px',
    marginBottom: '20px',
    width: props.width,
  }),
  outlinedInputInput: {
    padding: '12.5px 14px',
  },
  errorText: {
    color: '#E53935',
    marginBottom: '15px',
    marginTop: '0px',
  },
});
const BasicTextField = (props: BasicTextFieldProps) => {
  const classes = useStyles(props);
  const [blankValue, setBlankValue] = useState(false);

  let textFieldProps = props.TextFieldProps;
  textFieldProps = { ...textFieldProps, variant: 'outlined' };
  let inputProps = props.InputProps;
  inputProps = {
    ...inputProps,
    classes: { input: classes.outlinedInputInput },
  };
  return (
    <div className={classes.root}>
      <InputLabel htmlFor={props.id} className={classes.inputLabel}>
        {props.label}
      </InputLabel>
      <TextField
        id={props.id}
        className={classes.textField}
        inputProps={inputProps}
        value={blankValue ? '' : props.value}
        onChange={(e) => {
          if (blankValue === true) {
            setBlankValue(false);
          }
          props.onChange!(e);
        }}
        onFocus={() => {
          if (inputProps.type === 'tel' && (props.value === 0 || props.value === '0')) {
            setBlankValue(true);
          }
        }}
        onBlur={() => {
          if (inputProps.type === 'tel') {
            setBlankValue(false);
          }
        }}
        {...textFieldProps}
        error={props.error ? props.error : false}
        disabled={props.disabled ? props.disabled : false}
      />
      {props.error &&
      props.error_message !== undefined &&
      props.error_message !== '' ? (
        <FormHelperText className={classes.errorText}>
          {props.error_message}
        </FormHelperText>
      ) : null}
    </div>
  );
};

export default BasicTextField;
